import express from 'express';
import serialize from 'serialize-javascript';
import { renderToString } from 'react-dom/server';
import i18nsetup from './i18nServer';
import {serverMarkup} from '../markup';
const setupExpress = (src,store,App,assets) => {
	const server = i18nsetup(src,express(),(serv) => {

		serv
			.disable('x-powered-by')
			.use(express.static(process.env.RAZZLE_PUBLIC_DIR))
			.get('/*', (req, res) => {
				const context = {};
				const markup = renderToString(
					serverMarkup(
						store,
						req.i18n,
						context,
						req.url,
						App
					)
				);
				const finalState = store.getState();
				const { url } = context;

				if (url) {
					res.redirect(url);
				}
				else{
					const initialI18nStore = {};
					req.i18n.languages.forEach(l => {
						initialI18nStore[l] = req.i18n.services.resourceStore.data[l];
					})
					const initialLanguage = req.i18n.language;
					res.status(200).send(
						`<!doctype html>
						<html lang="">
						<head>
							<meta httpEquiv="X-UA-Compatible" content="IE=edge" />
							<meta charSet='utf-8' />
							<title>Welcome to Razzle</title>
							<meta name="viewport" content="width=device-width, initial-scale=1">
							<link rel="stylesheet" href="http://159.146.28.45/index.css">
							<link rel="stylesheet" type="text/css" charset="UTF-8" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css" />
							<link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
							<script src="${assets.client.js}" defer></script>
							<script>
								window.__PRELOADED_SATATE__ = ${serialize(finalState)}
								window.initialI18nStore = JSON.parse('${JSON.stringify(initialI18nStore)}');
								window.initialLanguage = '${initialLanguage}';
							</script>
						</head>
						<body>
							<div id="root">${markup}</div>
						</body>
						</html>`,
					);
				}
			})
		});
	return server
}

export default setupExpress